import { FileTypes } from "../../types";
const ExtendsSearch = {
  id: "ExtendsSearch",
  name: "ExtendsSearch",
  activate: function(molecule) {
    molecule.search.onChange((value) => {
      molecule.search.setValue(value);
    });
    molecule.search.onSelect((treeNode) => {
      if (treeNode.fileType === FileTypes.Folder) {
        const path = treeNode.id;
        const filenames = molecule.search.getState().result.results.filter((item) => item.filename.startsWith(path)).map((item) => item.filename);
        const isExpand = molecule.search.getExpandedKeys().some((val) => val.startsWith(path));
        if (isExpand) {
          molecule.search.removeExpandedKeys(filenames);
        } else {
          molecule.search.addExpandedKeys(filenames);
        }
      }
    });
  }
};
export {
  ExtendsSearch
};
